import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.css';

import HomePage from './pages/homepage/homepage.component';
import DetailsPage from './pages/details/details.component';

function App() {
  return (
    <div className="App">
      <Switch>
        <Route exact path="https://christianhalsell.github.io/react-pokemon/" component={HomePage} />
        <Route path="https://christianhalsell.github.io/react-pokemon/details/:id" component={DetailsPage} />
      </Switch>
    </div>
  );
}

export default App;
